.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .title {
    text-align: center;
    font-size: 3em;
    font-weight: bold;
  }
  
  form {
    display: flex;
    flex-direction: column;
    margin-top: 2em;
  }
  
  label {
    margin-bottom: 0.5em;
  }
  
  input {
    padding: 0.5em;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  button {
    margin-top: 1em;
    padding: 0.5em 1em;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }